import React, {useEffect, useState} from "react";
import {apiTweetCreate, apiCommentCreate, apiCommentList,apiChildCommentCreate,apiParentCommentList} from "./lookup";
import {AllowComment,AllowChildComment} from "./detail";

export function TweetCreate(props) {
    console.log(props);
    // ...props === username
    const {username} = props;
    const {didTweet} = props;
    const textAreaRef = React.createRef();
    const handleBackEndUpdate =
        (response, status) => {
            // backend api response
            if (status === 201) {
                didTweet(response);
            } else {
                console.log("response", response);
                alert("Please login to tweet")
            }
        };
    const handleSubmit = (event) => {
        event.preventDefault();
        const newVal = textAreaRef.current.value;
        // backend api request
        apiTweetCreate(newVal, handleBackEndUpdate);
        textAreaRef.current.value = '';
    };

    return <div className={props.className}>
        <form onSubmit={handleSubmit}>
                <textarea ref={textAreaRef} className='form-control' name='tweet'>
                </textarea>
            <button className='btn btn-primary my-3 mr-2'>Tweet</button>
            <button className='btn btn-primary my-3'>Upload</button>
        </form>
    </div>
}


export function CommentCreate(props) {
    // ...props === username
    const [showResults, setShowResults] = useState(false);
    const {tweetId} = props;
    const {didComment} = props;
    const [comments, setComments] = useState([]);
    const [newComment, setNewComment] = useState([]);
    const [didLookup, setDidLookUp] = useState(false);
    const [newComments, setNewComments] = useState(false);
    const textAreaRef = React.createRef();

    console.log("tweetId: ", tweetId);
    const handleBackEndUpdate =
        (response, status) => {
            // backend api response
            if (status === 201) {
                //   didComment(response);
                setNewComment(response[0].content);
                setNewComments(true);
            }
        };
    const handleSubmit = (event) => {
        event.preventDefault();
        const newVal = textAreaRef.current.value;
        // backend api request
        apiCommentCreate(tweetId, newVal, handleBackEndUpdate);
        textAreaRef.current.value = '';
    };

    const handleBackEndLookUp = (response, status) => {
        if (status === 200) {
            setComments(response);
            console.log("username:response", response)
        }
    };

    useEffect(() => {
        if (didLookup === false) {
            apiCommentList(tweetId, handleBackEndLookUp);
            setDidLookUp(true)
        }
    }, [tweetId, didLookup, setDidLookUp]);

    const onCommentClick = (event) => {
        event.preventDefault();
        if (setShowResults)
            setShowResults(false);
        setShowResults(true);
    };

    useEffect(() => {
        console.log("we has new comment", newComments);
        if (newComments === true) {
            apiCommentList(tweetId, handleBackEndLookUp);
            console.log("new comments list inside", comments);
            setNewComments(false)
        }
    }, [tweetId, newComments, setNewComments]);
    console.log("username:newComment", newComment);
    console.log("new comments list outside", comments);
    return  <div>
        <div className='text-left border border-dark p-3 mb-2'>{comments.map((item, index) => {
            return <React.Fragment><li className='mb-1'>@{item.username} said: {item.content}</li>
                <button onClick={onCommentClick}>comment</button>
                <AllowChildComment commentId={item.id} showResult={showResults}/>
            </React.Fragment>
        })}</div>
        <div className={props.className}>
            <form onSubmit={handleSubmit}>
                <textarea ref={textAreaRef} className='form-control' name='tweet'>
                </textarea>
                <button className='btn btn-primary my-3 mr-2'>Comment</button>
            </form>
        </div>
    </div>
}

export function CommentChildCreate(props) {
    // ...props === username
    const [showResults, setShowResults] = useState(false);
    const {commentId} = props;
    const {didComment} = props;
    const [comments, setComments] = useState([]);
    const [newComment, setNewComment] = useState([]);
    const [didLookup, setDidLookUp] = useState(false);
    const [newComments, setNewComments] = useState(false);
    const textAreaRef = React.createRef();

    console.log("commentId: ", commentId);
    console.log("currentChildComment: ", comments);
    const handleBackEndUpdate =
        (response, status) =>
        {
             console.log("child create status",status);
            // backend api response
            if (status === 201) {
                //   didComment(response);
                 console.log("child create response",response);
                setNewComment(response.content);
                setNewComments(true);
            }
        };
    const handleSubmit = (event) => {
        event.preventDefault();
        const newVal = textAreaRef.current.value;
        // backend api request
        apiChildCommentCreate(commentId, newVal, handleBackEndUpdate);
        textAreaRef.current.value = '';
    };

    const handleBackEndLookUp = (response, status) => {
         console.log("child status", status);
        if (status === 200) {
            setComments(response);
            console.log("child status", status)
        }
    };

    useEffect(() => {
        console.log("didlook",commentId);
        if (didLookup === false) {
            console.log("we in here",commentId)
            apiParentCommentList(commentId, handleBackEndLookUp);
            setDidLookUp(true)
        }
    }, [commentId, didLookup, setDidLookUp]);

    const onCommentClick = (event) => {
        event.preventDefault();
        if (setShowResults)
            setShowResults(false);
        setShowResults(true);
    };

    useEffect(() => {

        if (newComments === true) {
            apiParentCommentList(commentId, handleBackEndLookUp);
            console.log("new comments list inside", comments);
            setNewComments(false)
        }
    }, [commentId, newComments, setNewComments]);
    return  <div>
        <div className='text-left border border-dark p-3 mb-2'>{comments.map((item, index) => {
            return <React.Fragment><li className='mb-1'>@{item.username} said: {item.content}</li>
            </React.Fragment>
        })}</div>
        <div className={props.className}>
            <form onSubmit={handleSubmit}>
                <textarea ref={textAreaRef} className='form-control' name='tweet'>
                </textarea>
                <button className='btn btn-primary my-3 mr-2'>send</button>
            </form>
        </div>
    </div>
}

