import React, {useEffect, useState} from "react";
import {ActionBtn} from "./button";
import {UserDisplay, UserPicture} from "../profile";
import {apiCommentList, apiTweetDetail} from "./lookup";
import {CommentCreate,CommentChildCreate} from "./create";


export function ParentTweet(props) {
    const {tweet} = props;
    return tweet.parent ?
        <Tweet hideActions isReTweet retweeter={props.retweeter} className={' '} tweet={tweet.parent}/> : null
}


export function Tweet(props) {
    const {tweet, didRetweet, hideActions, isReTweet, retweeter} = props;
    const [comments, setComments] = useState([]);
    const [didLookup, setDidLookUp] = useState(false);
    const [actionTweet, setActionTweet] = useState(props.tweet ? props.tweet : null);
    const [showResults, setShowResults] = useState(false);
    const onCommentClick = (event) => {
        event.preventDefault();
        if (setShowResults)
            setShowResults(false);
        setShowResults(true);
    };
    let className = props.className ? props.className : 'col-10 mx-auto col-md-6';
    className = isReTweet === true ? `${className} p-2 border rounded` : className;
    const handlePerformAction = (newActionTweet, status) => {
        if (status === 200) {
            setActionTweet(newActionTweet);
        } else if (status === 201) {
            //let 'em known tweet created
            if (didRetweet) {
                didRetweet(newActionTweet)
            }
        }
    };

    const handleBackEndLookUp = (response, status) => {
        if (status === 200) {
            setComments(response)
        }
    };

    useEffect(() => {
        if (didLookup === false) {
            apiCommentList(tweet.id, handleBackEndLookUp);
            setDidLookUp(true)
        }
    }, [tweet.id, didLookup, setDidLookUp]);


    const path = window.location.pathname;
    const idRegex = /(?<id>\d+)/;
    const match = path.match(idRegex);
    const urlTweetId = match ? match.groups.id : -1;

    const isDetail = `${tweet.id}` === `${urlTweetId}`;
    const handleLink = (event) => {
        event.preventDefault();
        window.location.href = `/${tweet.id}`
    };
    const user = tweet.user;
// {(`${tweet.user}` && `${tweet.user}`)}
    //   console.log("username:parent", tweet.parent);
    return <div className={className}>
        {isReTweet === true &&
        <div className='ml-2 mb-2 '><span className='small text-muted'>ReTweet via  <UserDisplay
            user={retweeter}/></span></div>}
        <div className='d-flex'>
            <div className='col-1 mr-2'>
                <UserPicture user={tweet.user}/>
            </div>
            <div className='col-11'>
                <div>
                    <p><UserDisplay includeFullName user={tweet.user}/></p>
                    <p>{tweet.content}</p>

                    <ParentTweet tweet={tweet} retweeter={tweet.user}/>
                </div>
                <div className="btn btn-group-justified px-0">
                    {(actionTweet && hideActions !== true) && <React.Fragment>
                        <ActionBtn tweet={actionTweet} didPerformAction={handlePerformAction}
                                   action={{type: "like", display: "Likes"}}/>
                        <ActionBtn tweet={actionTweet} didPerformAction={handlePerformAction}
                                   action={{type: "unlike", display: "UnLike"}}/>
                        <ActionBtn tweet={actionTweet} didPerformAction={handlePerformAction}
                                   action={{type: "retweet", display: "ReTweet"}}/>
                        <button className='btn btn-dark btn-sm mr-1' onClick={onCommentClick}>Comment</button>
                    </React.Fragment>}


                    {isDetail !== false ? null :
                        <button className='btn btn-outline-primary btn-sm' onClick={handleLink}>
                            View
                        </button>}
                    <div className='mt-3'>â€‹
                        {showResults &&
                        <AllowComment tweet={tweet} showResult={showResults}
                                      commentList={comments}/>}
                    </div>
                </div>
            </div>
        </div>
    </div>
}

/**
 * @return {null}
 */
export function AllowComment(props) {
    const {commentList, showResult, tweet, handleNewComment,tweetId} = props;
    console.log("showResult",showResult);
     console.log("tweet_id",tweet.id);
    return commentList.length !== 0 && showResult ?
        <CommentCreate tweetId={tweet.id}/>
        : <CommentCreate tweetId={tweet.id}/>
}


export function AllowChildComment(props) {
    const {commentId, showResult} = props;
    console.log("child showResult",showResult);
    console.log("child commentId",commentId);
    console.log("child bool",commentId !== undefined && showResult);
    return commentId !== undefined && showResult ?
        <CommentChildCreate commentId={commentId}/>
        : null
}


