import React, {useEffect, useState} from "react";
import {apiTweetDetail} from "../tweets/lookup";
import {apiProfileDetail, apiProfileFollowToggle} from "./lookup";
import {UserDisplay, UserPicture} from "./profile_components";
import {DisplayCount} from "./utils";
import numeral from 'numeral'
const mystyle = {
      color: "white",
      backgroundColor: "DodgerBlue",
      padding: "10px",
      fontFamily: "Arial",
        height: '200px'
    };

function ProfileBadge(props) {
    const {user, didFollowToggle, profileLoading} = props;
    let currentVerb = (user && user.is_following) ? "UnFollow" : "Follow";
    currentVerb = profileLoading ? "Loading..." : currentVerb;
    console.log("username:verb", currentVerb);
    const handleFollowToggle = (event) => {
        console.log(event);
        event.preventDefault();
        if (didFollowToggle && !profileLoading) {
            didFollowToggle(currentVerb)
        }
    };
    return user ? <span>
        <div className='w-75 border border-dark text-center mb-2' style={{height: '200px',color:'white'}}>
            <p className='h-100 my-auto' style={{color:'black'}}>User Image Background</p>
        </div>
        <div className='d-flex'>
            <UserPicture user={user} hideLink/>
            <p><UserDisplay user={user} includeFullName hideLink/></p>
        </div>
        <div className='d-flex ml-5'>
            <p className='mr-3'><DisplayCount>{user.follower_count}</DisplayCount> {user.follower_count === 1 ? "follower" : "followers"}</p>
            |
            <p className='ml-3'><DisplayCount>{user.following_count}</DisplayCount> following</p>
        </div>

            <p className='ml-5'>bio: {user.bio}</p>
            <p className='ml-5'>location: {user.location}</p>

    <button className='btn btn-primary ml-5' onClick={handleFollowToggle}>{currentVerb}</button></span> : null
}

export function ProfileBadgeComponent(props) {
    const {username} = props;
    const [didLookup, setDidLookUp] = useState(false);
    const [profile, setProfile] = useState(null);
    const [profileLoading, setProfileLoading] = useState(false);
    const handleBackendLookUp = (response, status) => {
        if (status === 200) {
            console.log("username:res", response);
            setProfile(response)
        }
    };


    useEffect(() => {
        if (didLookup === false) {
            apiProfileDetail(username, handleBackendLookUp);
            setDidLookUp(true)
        }
    }, [username, didLookup, setDidLookUp]);

    const handleNewFollow = (actionVerb) => {
        apiProfileFollowToggle(username, actionVerb, (_response, status) => {
            console.log("username:res", _response, status);
            if (status === 200) {
                setProfile(_response)
                //  apiProfileDetail(username,handleBackendLookUp)
            }
            setProfileLoading(false)
        });
        setProfileLoading(true);
    };
    return didLookup === false ? "Loading..." : profile ?
        <ProfileBadge user={profile} didFollowToggle={handleNewFollow} profileLoading={profileLoading}/> : null
}